import styled from "styled-components";

import { Text, Title3 } from "../../assets/styles/component";
import Tag from "../Intro/tag";

import { Arrow } from "../../assets/images/Elements/arrow-up-right";

const STask = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  gap: 3.125rem;

  padding: 2.813rem;
  box-sizing: border-box;
  width: calc(32.5%);
  text-align: left;

  border: 2px solid rgba(255, 255, 255, 0.1);
  background: rgba(255, 255, 255, 0.05);
  border-radius: 1.75rem;

  transition: 0.5s;

  &:hover {
    background: rgba(255, 255, 255, 0.15);
  }

  & p {
    font-weight: 400;
    font-size: 1.125rem;
    line-height: 150%;
  }

  @media (max-width: 1600px) {
    width: calc(42% - 20px);
  }

  @media (max-width: 1300px) {
    width: calc(50% - 20px);
  }
`;

const TagsList = styled.div`
  display: flex;
  gap: 0.5rem;

  & div {
    position: relative;

    height: fit-content;

    border-radius: 3.125rem;
    padding: 0.625rem 20px;

    background: rgba(255, 255, 255, 0.05);

    transition: 0.25s ease-in-out;

    svg {
      opacity: 0;
      position: absolute;
      right: 1rem;
      top: 0.5rem;
      width: 1.375rem;
      height: 1.25rem;
      transition: 0.25s ease-in-out;
    }
  }

  & div:hover {
    background: rgba(255, 255, 255, 0.15);
    outline: 1px solid #fff;

    padding-right: 2.813rem;
    svg {
      opacity: 1;
    }
  }
`;

const Title = styled(Title3)`
  font-size: clamp(33px, 3vw, 45px);
  font-weight: 400;
  line-height: 110%;
  margin-bottom: 1.5rem;
`;

const Task = ({ title, desc, tags }) => {
  return (
    <STask>
      <div>
        <Title weight={300}>{title}</Title>
        <Text size={20}>{desc}</Text>
      </div>
      <TagsList>
        {tags.map((tag, index) => (
          <Tag key={index}>
            {tag} {<Arrow />}
          </Tag>
        ))}
      </TagsList>
    </STask>
  );
};

export default Task;
