import styled from "styled-components";

const TagStyled = styled.div`
  box-sizing: border-box;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: .625rem;

  font-weight: 300;
  font-size: .875rem;
  line-height: 150%;
  color: #ffffff;

  width: fit-content;
  height: 3.188rem;
  padding: .75rem 1rem;

  outline: none;

  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius : 1rem;

  cursor: pointer;

  &:hover {
    background: rgba(255, 255, 255, 0.15);
  }

  transition: background 0.5s;
`;

const Tag = ({ children }) => {
  return <TagStyled>{children}</TagStyled>;
};

export default Tag;
