import { useState, useEffect, useRef } from "react";
import styled from "styled-components";
import { css } from "styled-components";

import { EngLoc } from "../../assets/constants/localizations";
import { dribble_right } from "../../assets/styles/animations";

import { Title2 } from "../../assets/styles/component";
import Task from "./taskDesktop";

import { YouTube } from "../../assets/images/Socials/Youtube";
import { Odnoklassniki } from "../../assets/images/Socials/Odnoklassniki";
import { Vk } from "../../assets/images/Socials/Vk";
import { Telegram } from "../../assets/images/Socials/Telegram";
import { Instagram } from "../../assets/images/Socials/Instagram";
import { YandexZen } from "../../assets/images/Socials/YandexZen";

const STasks = styled.section`
  position: relative;
  overflow: hidden;
  padding: 9.375rem 0 180px 0;

  background: radial-gradient(
      43.18% 87.18% at 67.39% 25.75%,
      #ff7cda 0%,
      rgba(255, 124, 218, 0) 100%
    ),
    radial-gradient(
      25.12% 79.02% at 32.93% 125.8%,
      #00aee8 0%,
      rgba(0, 174, 232, 0) 100%
    ),
    radial-gradient(
      38.5% 124.42% at 61.09% 172.86%,
      #00aee8 0%,
      rgba(0, 174, 232, 0) 100%
    ),
    radial-gradient(
      60.8% 152.7% at -13.59% 11.65%,
      #00aee8 0%,
      rgba(0, 174, 232, 0) 100%
    ),
    radial-gradient(
      64.16% 160.68% at 112.44% 38.89%,
      #7400e8 0%,
      rgba(116, 0, 232, 0) 100%
    ),
    radial-gradient(
      26.08% 44.57% at 5.86% 120.25%,
      #7400e8 0%,
      rgba(116, 0, 232, 0) 100%
    ),
    #000f67;

  & * {
    z-index: 1;
  }
`;

const TasksInner = styled.div`
  display: flex;
  flex-direction: column;
  gap: 120px;
  margin: 0 8.3%;
  max-width: 100rem;

  @media (min-width: 3000px) {
    margin: 0 auto;
  }

  @media (max-width: 1200px) {
    width: calc(100% - 10%);
    margin: 0 3%;
  }
`;

const Title = styled(Title2)`
  @media (max-width: 1200px) {
    font-weight: 400;
    font-size: 4.75rem;
    line-height: 110%;
  }
`;

const TaskList = styled.div`
  display: flex;
  gap: 1.25rem;
  flex-wrap: wrap;

  
  & > div {
    display: flex;
    gap: 1.25rem;
   
    @media (max-width: 1300px) {
      flex-wrap: wrap;
    }

    @media (min-width: 2500px) {
      max-width: 100rem;
      margin: auto;
    }
  }

  & > div:last-child {
    flex-direction: row-reverse;

    @media (max-width: 1300px) {
      flex-direction: row;
    }
  }

  & > div > div {
    opacity: 0;
    ${({ view }) => !view && dribble_right}
  }
`;

const Socials = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 1.625rem;

  width: 32.5%;

  & > p {
    font-weight: 400;
    font-size: 1.25rem;
    line-height: 150%;
    color: #fff;
    max-width: 420px;
  }

  & > div {
    display: flex;
    gap: 1.063rem;

    & > svg {
      aspect-ratio: 1/1;
      width: 1.75rem;
      height: 1.75rem;
    }
  }
`;

const Tasks = ({ language }) => {
  const [view, setView] = useState(true);

  const intersectionObserver = new IntersectionObserver((entries) => {
    if (entries[0].intersectionRatio <= 0) return;
    setView(false);
  });

  useEffect(() => {
    intersectionObserver.observe(elem.current && elem.current);
  }, []);

  const elem = useRef(null);

  return (
    <STasks ref={elem}>
      <TasksInner>
        <Title>
          {language === "Ru" ? "Решаем задачи:" : EngLoc.tasks.title}
        </Title>
        <TaskList view={view}>
          <div>
            <Task
              title={
                language === "Ru"
                  ? "Медийная реклама"
                  : EngLoc.tasks.tasks_list[0].title
              }
              desc={
                language === "Ru"
                  ? "Вывод бренда на рынок, увеличение узнаваемости, масштабные кампании с федеральным и глобальным охватом"
                  : EngLoc.tasks.tasks_list[0].desc
              }
              tags={["TeleJet", "RocketVideo"]}
            />
            <Task
              title={
                language === "Ru"
                  ? "Конверсии на сайте"
                  : EngLoc.tasks.tasks_list[1].title
              }
              desc={
                language === "Ru"
                  ? "Конверсии в действия или покупки на сайтах и продажи"
                  : EngLoc.tasks.tasks_list[1].desc
              }
              tags={["RocketVideo"]}
            />
          </div>
          <div>
            <Task
              title={
                language === "Ru"
                  ? "Аналитика и оптимизация"
                  : EngLoc.tasks.tasks_list[3].title
              }
              desc={
                language === "Ru"
                  ? "Подбор аккаунтов, проверка на накрутку и антифрод, подробная отчетность и оптимизация"
                  : EngLoc.tasks.tasks_list[3].desc
              }
              tags={["TeleJet", "RocketVideo", "SocialJet"]}
            />
            <Task
              title={
                language === "Ru"
                  ? "Привлечение трафика"
                  : EngLoc.tasks.tasks_list[2].title
              }
              desc={
                language === "Ru"
                  ? "Привлечение трафика на сайт, подписчиков в канал, переходов в чат-бот и многое другое."
                  : EngLoc.tasks.tasks_list[2].desc
              }
              tags={["SocialJet"]}
            />
            <Socials>
              <p>
                Решения SocialJet охватывают большинство популярных платформ:
              </p>
              <div>
                <Telegram />
                <Vk />
                <Instagram />
                <Odnoklassniki />
                <YandexZen />
                <YouTube />
              </div>
            </Socials>
          </div>
        </TaskList>
      </TasksInner>
    </STasks>
  );
};

export default Tasks;
